
import InputGroup from "./InputGroup";
import tab from './../../group/tab'
import Toast from 'vux/src/components/toast/index.vue'

import {getVerCode,wxBindMobile} from '@/api/user'
export default {
name: "login",
data() {
return {
phone: "",
verifyCode: "",
title:'绑定手机号',
errors: {},
btnTitle: "获取验证码",
disabled: false,
cancal_img:require('./../../../assets/search/返回.png'),
errorText:'',
success:'',
show_success:false,
show_error:false,
userInfo:''
};
},
computed: {
isClick() {
if (!this.phone || !this.verifyCode) return true;
else return false;
}
},
mounted(){

this.userInfo = JSON.parse(this.$route.params.id);
},
methods: {
handleLogin() {

this.errors = {};

let data={
verCode:this.verifyCode,
mobile:this.phone,
userId:this.userInfo.userId
}

wxBindMobile(data).then(res=>{
if(res.code==200){
console.log(res.code)
this.$store.commit("setUserInfo",this.userInfo);
this.success='绑定成功';
this.show_success=true;
let _this=this;
setTimeout(function(){
_this.$router.go(-3)
},500)
}else{
this.errorText="绑定失败";
this.show_error=true;
}
})

},
getVerifyCode() {
if (this.validatePhone()) {
this.validateBtn();

let data={
userName:this.phone
}
getVerCode(data).then(res=>{
console.log(res.data)
})

}
},
validateBtn() {
let time = 60;
let timer = setInterval(() => {
if (time == 0) {
clearInterval(timer);
this.btnTitle = "获取验证码";
this.disabled = false;
} else {

this.btnTitle = time + "秒后重试";
this.disabled = true;
time--;
}
}, 1000);
},
validatePhone() {

if (!this.phone) {
this.errors = {
phone: "手机号码不能为空"
};
return false;
} else if (!/^1[23456789]\d{9}$/.test(this.phone)) {
this.errors = {
phone: "请填写正确的手机号码"
};
return false;
} else {
this.errors = {};
return true;
}
}
},
components: {
InputGroup,
Toast,
tab
}
};



